.fork-me {
  width: 150px;
  height: 150px;
  overflow: hidden;
  position: absolute;
  display: block;
  pointer-events: none;
}
.fork-me a {
  position: absolute;
  width: 150px * 2;
  height: 150px * 0.23;
  top: 50%;
  left: 50%;
  margin-left: (150px * 2 / 2) * -1;
  margin-top: (150px * 0.23 / 2) * -1;
  pointer-events: auto;
  white-space: nowrap;
  display: block;
  text-align: center;
  text-decoration: none;
  line-height: 150px * 0.23;
  font-family: "Collegiate", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";
  font-size: 150px * 0.23 / 2.5;
  font-weight: 700;
  letter-spacing: 0.1px;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.5);
  z-index: 9999;
}
.fork-me a:before {
  content: " ";
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  z-index: -1;
  opacity: 0.8;
  border-width: 1px 0px;
  border-style: dotted;
  border-color: inherit;
